.nav{
    position: fixed;
    top:0;
    padding: 20px;
    
    width: 100%;
    height: 30px;
    z-index: 1;
    transition-timing-function: ease-in;
    transition: all 0.5s;
}
.nav_black{
    background-color: black;
}
.nav_contents{
    display: flex;
    justify-content: space-between;
}
.nav_logo{
    position:fixed;
    top:10px;
    left:0;
    object-fit: contain;
    padding-left:20px;
    width:80px;    
    cursor:pointer;
    

}
.nav_avatar {
    position: fixed;
    right:0;
    margin-right: 20px;
    cursor:pointer;
    width:30p;
    height:30px;
}